.popup {
  position: fixed;
  overflow: auto;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  display: none;
  z-index: 50;
}

.popup--opened {
  display: flex;
  justify-content: center;
}

.popup__wrapper {
  align-self: center;
  padding-top: 188px;
  padding-left: 45px;
  padding-right: 45px;
  padding-bottom: 60px;
  box-sizing: border-box;
  width: 430px;
  height: 586px;
  display: flex;
  flex-direction: column;
  z-index: 500;
  background-color: $blog-bckg-login;
  position: relative;

  h6 {
    font-weight: 700;
    font-size: 34px;
    line-height: 44.2px;
    color: $main-black;
    font-family: $font-header;
    align-self: center;
    margin-top: 0;
    margin-bottom: 32px;
  }

  &::before {
    content: "";
    position: absolute;
    z-index: -1;
    width: 430px;
    top: 0;
    left: 0;
    height: 138px;
    background-image: url("../img/bckg-popup-dkt.jpg");
    background-repeat: no-repeat;
    background-size: 430px 138px;

    @media (min-resolution: 2dppx) {
      background-image: url("../img/bckg-popup-dkt@2x.jpg");
      background-size: 430px 138px;
    }
  }

  form {
    display: flex;
    flex-direction: column;

    input {
      width: 340px;
      height: 30px;
      box-sizing: border-box;
      border: none;
      border-bottom: 1px solid $main-black;
      padding-bottom: 9px;
      background-color: transparent;

      &::placeholder {
        font-weight: 500;
        font-size: 14px;
        line-height: 21px;
        font-family: $font-main;
        color: $blog-date-numbers;
      }

      &:first-of-type {
        margin-bottom: 37px;
      }
    }

    a {
      font-weight: 500;
      font-size: 12px;
      line-height: 18px;
      font-family: $font-main;
      color: $blog-date-numbers;
      text-decoration: none;
      margin-top: 5px;
      align-self: flex-end;
      margin-bottom: 45px;
    }

    span {
      font-weight: 500;
      font-size: 14px;
      line-height: 21px;
      font-family: $font-main;
      color: $blog-date-numbers;
      align-self: center;
      margin-right: 13px;

      a {
        margin-left: 10px;
        color: $main-black;
      }
    }
  }
}

.popup__wrapper-close {
  position: absolute;
  top: 20px;
  right: 20px;
  height: 14px;
  width: 14px;
  border: none;
  padding: 0;
  background-color: transparent;
  background-image: url("../img/icon-cross.svg");
  background-repeat: no-repeat;
  background-size: 14px 14px;
  cursor: pointer;

  &:hover {
    -webkit-transform: scale(1.2);
    -ms-transform: scale(1.2);
    transform: scale(1.2);
  }

  &:focus {
    -webkit-transform: rotateZ(20deg);
    -ms-transform: rotateZ(20deg);
    transform: rotateZ(20deg);
  }
}

.popup__wrapper-submit {
  width: 340px;
  height: 68px;
  box-sizing: border-box;
  border: 1px solid $arrows-lines;
  background-color: $arrows-lines;
  padding-top: 23px;
  padding-bottom: 28px;
  padding-left: 140px;
  padding-right: 140px;
  color: $header-footer;
  font-weight: 500;
  font-size: 16px;
  line-height: 20.8px;
  font-family: $font-main;
  cursor: pointer;
  margin-bottom: 13px;

  &:hover {
    background-color: $header-footer;
    color: $arrows-lines;
  }

  &:active {
    background-color: $blog-date-numbers;
    color: $arrows-lines;
  }

  &:focus {
    background-color: $header-footer;
    color: $arrows-lines;
  }
}

@media (max-width: $tablet-width) {
  .popup__wrapper {
    width: 442px;
    padding-left: 50px;
    padding-right: 51px;

    h6 {
      margin-bottom: 32px;
    }

    &::before {
      width: 442px;
      background-image: url("../img/bckg-popup-tab.jpg");
      background-size: 442px 138px;

      @media (min-resolution: 2dppx) {
        background-image: url("../img/bckg-popup-tab@2x.jpg");
        background-size: 442px 138px;
      }
    }

    form {
      input {
        &:first-of-type {
          margin-bottom: 35px;
        }
      }

      span {
        margin-right: 0;

        a {
          margin-left: 10px;
        }
      }
    }
  }

  .popup__wrapper-close {
    height: 20px;
    width: 20px;
    background-size: 20px 20px;
  }

  .popup__wrapper-submit {
    padding-top: 25px;
    padding-left: 142px;
    margin-bottom: 15px;
  }
}

@media (max-width: $mobile-width) {
  .popup__wrapper {
    width: 270px;
    height: 470px;
    padding-top: 120px;
    padding-left: 14px;
    padding-right: 15px;
    padding-bottom: 45px;

    form {
      display: flex;
      flex-direction: column;
      width: 240px;

      input {
        width: 240px;
        height: 25px;
        padding-bottom: 15px;

        &:first-of-type {
          margin-bottom: 35px;
        }
      }

      a {
        margin-bottom: 35px;
      }

      span {
        a {
          margin-left: 5px;
        }
      }
    }

    &::before {
      width: 270px;
      background-image: url("../img/bckg-popup-mob.jpg");
      background-size: 270px 84px;

      @media (min-resolution: 2dppx) {
        background-image: url("../img/bckg-popup-mob@2x.jpg");
        background-size: 270px 84px;
      }
    }

    h6 {
      font-size: 24px;
      line-height: 31.2px;
      margin-bottom: 33px;
    }
  }

  .popup__wrapper-close {
    top: 14px;
    right: 14px;
    height: 14px;
    width: 14px;
    background-size: 14px 14px;
  }

  .popup__wrapper-submit {
    width: 240px;
    padding-left: 96px;
    padding-right: 95px;
  }
}
