.page-header {
  display: flex;
  justify-content: space-between;
  box-sizing: border-box;
  padding-top: 0;
  margin: 0 auto;
  width: 100%;
  padding-left: 127px;
  padding-right: 98px;
  align-self: center;
  max-width: 1366px;
  height: 115px;
  border-bottom: 1px solid $header-line;
  padding-bottom: 19px;
  font-family: $font-main;

  form {
    width: 160px;
    height: 25px;
    position: relative;
    align-self: center;
    padding-top: 10px;

    input {
      border: none;
      font-weight: 500;
      font-size: 14px;
      line-height: 21px;
      font-family: $font-main;
      padding: 0;
      width: 155px;
      height: 15px;
    }

    &::before {
      content: "";
      position: absolute;
      z-index: -1;
      width: 14px;
      top: 15px;
      left: -30px;
      height: 14px;
      background-image: url("../img/icon-search-black.svg");
      background-repeat: no-repeat;
      background-size: 14px 14px;
    }
  }

  div {
    display: flex;
    height: 10px;
    align-self: center;
    margin-right: -13px;
    a {
      padding: 0;
      font-weight: 500;
      font-size: 14px;
      line-height: 21px;
      font-family: $font-main;
      background-color: transparent;
      cursor: pointer;
      height: 10px;
      text-decoration: none;
      color: $main-black;
      width: 38px;

      &:nth-of-type(n+2) {
        margin-left: 35px;
      }

      &:nth-of-type(2n) {
        width: 45px;
      }
    }
  }

  a {
    height: 66px;
    align-self: center;
  }
}

.page-header__logo {
  margin-right: 68px;
  padding-top: 30px;

  svg {
    &:last-of-type {
      display: none;
    }
  }
}

.page-header__tab {
  display: none;
}

.page-header--card {
  &::before {
    display: none;
  }

  &::after {
    display: none;
  }
}

.page-header--popup {
  &::before {
    display: none;
  }

  &::after {
    display: none;
  }
}

@media (max-width: $tablet-width) {
  .page-header {
    padding-left: 45px;
    padding-right: 57px;
    min-width: 768px;
    height: 126px;
    border: none;
    padding-bottom: 0;
    justify-content: space-between;
    padding-top: 0;

    &::before {
      top: 126px;
      background-color: $arrows-lines;
      content: "";
      position: absolute;
      z-index: -2;
      width: 100vw;
      left: 0;
      height: 463px;
    }

    &::after {
      top: 126px;
      background-image: url("../img/bckg-main-tab.jpg");
      content: "";
      position: absolute;
      z-index: -1;
      width: 100vw;
      left: 0;
      background-position: center;
      height: 463px;
      background-repeat: no-repeat;
      background-size: cover;

      @media (min-resolution: 2dppx) {
        background-image: url("../img/bckg-main-tab@2x.jpg");
      }
    }

    form {
      display: none;
    }

    div {
      height: 20px;

      a {
        &:nth-of-type(n+1) {
          display: none;
        }

        &:last-of-type {
          display: block;
          height: 18px;
          width: 20px;
          color: transparent;
          background-image: url("../img/icon-cart-black.svg");
          background-repeat: no-repeat;
          background-size: 20px 18px;
          margin-right: 0;
        }
      }
    }
  }

  .page-header__logo {
    margin-right: -30px;
    padding-top: 0;
  }

  .page-header--catalog {
    &::before {
      height: 261px;
    }

    &::after {
      height: 261px;
      background-image: url("../img/bckg-catalog-tab.jpg");

      @media (min-resolution: 2dppx) {
        background-image: url("../img/bckg-catalog-tab@2x.jpg");
      }
    }
  }

  .page-header__tab {
    display: block;
    align-self: center;
    border: none;
    background-color: transparent;
    background-image: url("../img/icon-menu-black.svg");
    background-repeat: no-repeat;
    background-size: 25px 16px;
    width: 25px;
    height: 16px;
  }

  .page-header__tab--opened {
    background-image: url("../img/icon-menu-white.svg");
  }
}

@media (max-width: $mobile-width) {
  .page-header {
    padding-top: 0;
    padding-left: 15px;
    padding-right: 27px;
    min-width: 320px;
    height: 92px;
    margin-bottom: 20px;

    &::before {
      top: 92px;
    }

    &::after {
      top: 92px;
      height: 463px;
      background-image: url("../img/bckg-main-mob.jpg");

      @media (min-resolution: 2dppx) {
        background-image: url("../img/bckg-main-mob@2x.jpg");
      }
    }

    .page-header__logo {
      width: 52px;
      height: 52px;

      svg {
        &:last-of-type {
          display: block;
        }

        &:first-of-type {
          display: none;
        }
      }
    }
  }

  .page-header--catalog {
    &::after {
      background-image: url("../img/bckg-catalog-mob.jpg");
      height: 261px;

      @media (min-resolution: 2dppx) {
        background-image: url("../img/bckg-catalog-mob@2x.jpg");
        background-size: 320px 261px;
      }
    }
  }

  .page-header--card {
    margin-bottom: 0;
  }
}
