.popup-menu {
  position: fixed;
  max-height: 100%;
  overflow-y: auto;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  display: none;
  z-index: 40;
}

.popup-menu--opened {
  display: flex;
  justify-content: center;
}

.popup-menu__wrapper {
  max-height: 100%;
  overflow-y: auto;
  align-self: center;
  padding-top: 30px;
  padding-left: 45px;
  padding-right: 45px;
  padding-bottom: 80px;
  box-sizing: border-box;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  z-index: 500;
  position: relative;

  &::before {
    content: "";
    position: absolute;
    z-index: -1;
    width: 100%;
    top: 0;
    left: 0;
    height: 140%;
    background-color: $arrows-lines;
  }

  form {
    min-width: 678px;
    height: 40px;
    position: relative;
    align-self: flex-start;
    width: 100%;

    input {
      border: none;
      font-weight: 500;
      font-size: 14px;
      line-height: 21px;
      font-family: $font-main;
      padding: 12px;
      min-width: 678px;
      width: 100%;
      height: 40px;
      border-radius: 21px;
      background-color: $input-menu;
      box-sizing: border-box;
    }

    &::before {
      content: "";
      position: absolute;
      z-index: -1;
      width: 16px;
      top: 12px;
      left: 13px;
      height: 16px;
      background-image: url("../img/icon-search-white.svg");
      background-repeat: no-repeat;
      background-size: 16px 16px;
    }
  }
}

.popup-menu__wrapper-nav {
  margin-top: 45px;
  margin-bottom: 40px;

  ul {
    display: flex;
    flex-direction: column;
    list-style: none;
    padding: 0;
    margin: 0;

    li {
      &:nth-of-type(n+2) {
        margin-top: 25px;
      }

      &:nth-of-type(n+3) {
        margin-top: 20px;
      }
    }

    a {
      color: $header-footer;
      font-weight: 500;
      font-size: 24px;
      line-height: 36px;
      text-decoration: none;
      font-family: $font-main;
    }
  }
}

.popup-menu__wrapper-nav-info {
  display: flex;
  flex-direction: column;
  list-style: none;
  padding: 0;
  margin: 0;
  margin-bottom: 45px;

  li {
    &:nth-of-type(n+2) {
      margin-top: 20px;
    }

    &:nth-of-type(n+3) {
      margin-top: 17px;
    }
  }

  a {
    color: $info-menu;
    font-weight: 500;
    font-size: 18px;
    line-height: 23.4px;
    text-decoration: none;
    font-family: $font-main;
  }
}

.popup-menu__wrapper-nav-login {
  padding: 0;
  font-weight: 500;
  font-size: 18px;
  line-height: 23.4px;
  font-family: $font-main;
  background-color: transparent;
  cursor: pointer;
  text-decoration: none;
  color: $header-footer;
}

.popup-menu__wrapper-header {
  display: flex;
  justify-content: space-between;
  margin-bottom: 30px;
}

.popup-menu__wrapper-header-tab {
  align-self: center;
  border: 0;
  background-color: transparent;
  background-image: url("../img/icon-menu-white.svg");
  background-repeat: no-repeat;
  background-size: 25px 16px;
  width: 25px;
  height: 16px;
  margin-top: -3px;
}

.popup-menu__wrapper-header-logo {
  align-self: center;
  margin-right: 5px;
  margin-top: 2px;

  svg {
    &:last-of-type {
      display: none;
    }
  }
}

.popup-menu__wrapper-header-menu {
  align-self: center;
  height: 18px;
  width: 20px;
  background-image: url("../img/icon-cart-white.svg");
  background-repeat: no-repeat;
  background-size: 20px 18px;
  padding: 0;
  background-color: transparent;
  border: none;
  margin-top: -2px;
}

@media (max-width: $mobile-width) {
  .popup-menu__wrapper {
    padding-top: 20px;
    padding-left: 15px;
    padding-right: 15px;
    padding-bottom: 50px;

    &::before {
      content: "";
      position: absolute;
      z-index: -1;
      width: 100%;
      top: 0;
      left: 0;
      height: 140%;
      background-color: $arrows-lines;
    }

    form {
      min-width: 290px;

      input {
        min-width: 290px;
      }

      &::before {
        top: 12px;
        left: 13px;
      }
    }
  }

  .popup-menu__wrapper-nav {
    margin-top: 36px;
    margin-bottom: 28px;

    ul {
      li {
        &:nth-of-type(n+2) {
          margin-top: 20px;
        }

        &:nth-of-type(n+3) {
          margin-top: 22px;
        }

        &:nth-of-type(n+5) {
          margin-top: 21px;
        }
      }
    }
  }

  .popup-menu__wrapper-nav-info {
    margin-bottom: 33px;
  }

  .popup-menu__wrapper-header {
    margin-bottom: 30px;
  }

  .popup-menu__wrapper-header-logo {
    align-self: center;
    margin-right: 5px;
    margin-top: 0;

    svg {
      &:last-of-type {
        display: block;
      }

      &:first-of-type {
        display: none;
      }
    }
  }
}
