.page-footer {
  display: flex;
  align-self: center;
  margin: 0 auto;
  box-sizing: border-box;
  width: 100%;
  padding-left: 97px;
  padding-right: 100px;
  max-width: 1366px;
  background-color: $blog-header-footer;
  min-height: 338px;
  font-family: $font-main;
  padding-top: 90px;
  padding-bottom: 100px;
  position: relative;

  &::before {
    background-color: $blog-header-footer;
    content: "";
    position: absolute;
    z-index: -2;
    width: 100vw;
    top: 0;
    left: calc(-50vw + 50%);
    min-height: 100%;
  }
}

.page-footer__follow {
  min-width: 500px;

  h6 {
    font-weight: 700;
    font-size: 34px;
    line-height: 44.2px;
    color: $header-footer;
    font-family: $font-header;
    margin-bottom: 10px;
    margin-top: 0;
    letter-spacing: 0.03em;
  }

  p {
    font-weight: 400;
    font-size: 16px;
    line-height: 20.8px;
    color: $blog-date-numbers;
    font-family: $font-main;
    margin-top: 0;
    margin-bottom: 40px;
  }

  form {
    position: relative;

    input {
      background-color: transparent;
      border: none;
      border-bottom: 1px solid $header-footer;
      max-width: 500px;
      padding-bottom: 13px;
      color: $header-footer;
      font-size: 14px;
      box-sizing: border-box;
      width: 100%;

      &::placeholder {
        font-weight: 500;
        font-size: 14px;
        line-height: 21px;
        color: $blog-date-numbers;
        font-family: $font-main;
      }
    }

    button {
      position: absolute;
      top: 3px;
      right: 40px;
      border: none;
      background-color: transparent;
      font-weight: 500;
      font-size: 16px;
      line-height: 20.8px;
      color: $header-footer;
      font-family: $font-main;
      padding: 0;
      cursor: pointer;

      &:hover {
        color: $arrows-lines;

        &::before {
          background-image: url("../img/icon-vector-brown.svg");
          transform: rotate(90deg);
        }
      }
    }

    &::before {
      content: "";
      position: absolute;
      width: 29px;
      top: 9px;
      right: 0;
      height: 9px;
      background-image: url("../img/icon-vector-white.svg");
      background-size: 29px 9px;
      background-repeat: no-repeat;
    }
  }
}

.page-footer__about {
  display: flex;
  justify-content: space-between;
  margin-left: 91px;
  margin-top: 8px;
  max-width: 582px;
  width: 100%;
  box-sizing: border-box;

  ul {
    margin: 0;
    padding: 0;
    list-style: none;

    a {
      text-decoration: none;
      font-weight: 500;
      font-size: 16px;
      line-height: 20.8px;
      color: $header-footer;
      font-family: $font-main;

      svg {
        margin-right: 13px;
      }

      &:hover {
        color: $arrows-lines;
      }
    }

    li {
      text-decoration: none;
      font-weight: 500;
      font-size: 16px;
      line-height: 20.8px;
      color: $header-footer;
      font-family: $font-main;

      &:nth-of-type(n+2) {
        margin-top: 22px;
      }
    }

    &:last-of-type {
      margin-left: 28px;
      margin-right: -20px;
    }
  }
}

.page-footer__about-social {
  display: flex;
  flex-direction: column;

  span {
    font-weight: 500;
    font-size: 16px;
    line-height: 20.8px;
    color: $header-footer;
    font-family: $font-main;
  }

  a {
    &:hover {
      span {
        color: $arrows-lines;
      }
    }
  }
}

.page-footer__about-social-item {
  svg {
    &:last-of-type {
      display: none;
    }
  }
}

@media (max-width: $tablet-width) {
  .page-footer {
    flex-direction: column;
    padding-left: 45px;
    padding-right: 140px;
    max-width: 768px;
    min-height: 446px;
    padding-top: 50px;
    padding-bottom: 60px;
  }

  .page-footer__follow {
    min-width: 560px;

    form {
      input {
        min-width: 560px;
        padding-bottom: 10px;
      }

      button {
        top: 5px;
        right: 63px;
        border: none;
        background-color: transparent;
        font-weight: 500;
        font-size: 16px;
        line-height: 20.8px;
        color: $header-footer;
        font-family: $font-main;
        padding: 0;
        cursor: pointer;
      }

      &::before {
        top: 10px;
        right: 22px;
      }
    }

    p {
      margin-bottom: 42px;
    }
  }

  .page-footer__about {
    margin-left: 0;
    margin-top: 48px;

    ul {
      &:nth-of-type(n+1) {
        li {
          min-width: 168px;
        }
      }

      &:nth-of-type(n+2) {
        margin-left: 30px;

        li {
          min-width: 116px;

          &:nth-of-type(n+2) {
            margin-top: 23px;
          }
        }
      }

      &:last-of-type {
        margin-right: 7px;
      }

      &:nth-of-type(n+3) {
        li {
          min-width: 115px;
        }
      }
    }
  }
}

@media (max-width: $mobile-width) {
  .page-footer {
    padding-left: 15px;
    padding-right: 15px;
    max-width: 320px;
    min-height: 773px;
  }

  .page-footer__about-social {
    flex-direction: row;

    li {
      width: 20px;
      height: 21px;

      &:nth-of-type(n+2) {
        margin-left: 22px;
      }

      &:nth-of-type(n+3) {
        margin-left: 29px;
      }

      &:nth-of-type(n+4) {
        margin-left: 32px;
      }

      span {
        display: none;
      }
    }
  }

  .page-footer__follow {
    min-width: 290px;

    form {
      input {
        min-width: 287px;
        padding-bottom: 11px;
      }

      button {
        right: 40px;
        top: 3px;
      }

      &::before {
        top: 8px;
        right: 0;
      }
    }

    h6 {
      margin-bottom: 15px;
      width: 213px;
    }

    p {
      width: 226px;
      margin-bottom: 40px;
    }
  }

  .page-footer__about {
    flex-direction: column;
    align-self: center;
    width: 290px;
    min-width: 290px;
    margin-top: 47px;

    ul {
      width: 290px;

      &:nth-of-type(n+2) {
        margin-left: 0;
        margin-top: 43px;

        li {
          &:nth-of-type(n+3) {
            margin-top: 22px;
          }
        }
      }

      &:nth-of-type(n+3) {
        flex-direction: row;
        margin-top: 45px;

        li {
          min-width: 12px;

          &:nth-of-type(n+2) {
            margin-top: 0;
          }
        }
      }

      a {
        svg {
          margin-right: 0;
        }
      }
    }
  }

  .page-footer__about-social-item {
    svg {
      &:last-of-type {
        display: block;
      }

      &:first-of-type {
        display: none;
      }
    }
  }
}
