.popup-filter {
  position: fixed;
  overflow: auto;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  display: none;
  z-index: 50;
}

.popup-filter--opened {
  display: flex;
  justify-content: center;
}

.popup-filter__wrapper-filters {
  width: 458px;
  align-self: center;
}

.popup-filter__wrapper-filters-closebutton {
  background-image: url("../img/icon-cross-tab.svg");
  width: 32px;
  height: 32px;
  background-repeat: no-repeat;
  background-size: 32px 32px;
  background-color: transparent;
  border: none;
  position: absolute;
  right: 17px;
  top: 10px;
  padding: 0;
}

.popup-filter__wrapper {
  align-self: center;
  box-sizing: border-box;
  width: 458px;
  height: 100%;
  display: flex;
  flex-direction: column;
  z-index: 500;
  position: relative;
  background-color: $header-footer;

  form {
    display: flex;
    flex-direction: column;
    width: 458px;
    background-color: $header-footer;
    height: 100%;

    fieldset {
      border: none;
      margin: 0;
      position: relative;
      min-height: 92px;
      padding-right: 88px;
      padding-left: 24px;
      padding-bottom: 38px;
      padding-top: 108px;
      width: 458px;
      box-sizing: border-box;

      .popup-filter__wrapper-filters-menu {
        position: absolute;
        background-color: transparent;
        border: none;
        background-image: url("../img/icon-arrow.svg");
        width: 23px;
        height: 12px;
        background-size: 23px 12px;
        background-repeat: no-repeat;
        top: 65px;
        right: 73px;
        cursor: pointer;
      }

      .popup-filter__wrapper-filters-menu--opened {
        background-image: url("../img/icon-arrow.svg");
        transform: rotate(180deg);
      }

      &:nth-of-type(n+1) {
        border-top: 1px solid $arrows-lines;
      }

      &:last-of-type {
        border-bottom: 1px solid $arrows-lines;
        padding-right: 88px;
        padding-left: 50px;
        padding-bottom: 71px;
        padding-top: 91px;
        width: 458px;
      }

      legend {
        font-weight: 500;
        font-size: 18px;
        line-height: 23.4px;
        color: $main-black;
        font-family: $font-main;
        padding: 0;
        position: absolute;
        top: 60px;
      }

      &:nth-of-type(n+2) {
        padding-bottom: 38px;
        padding-top: 50px;

        legend {
          top: 33px;
        }

        .popup-filter__wrapper-filters-menu {
          top: 40px;
        }
      }

      &:nth-of-type(n+4) {
        padding-bottom: 70px;
        padding-top: 90px;
        padding-left: 35px;

        legend {
          top: 33px;
          left: 23px;
        }
      }
    }

    ul {
      list-style: none;
      padding: 0;

      li {
        font-weight: 500;
        font-size: 16px;
        line-height: 20.8px;
        color: $main-black;
        font-family: $font-main;

        &:nth-of-type(n+2) {
          margin-top: 18px;
        }
      }
    }

    label {
      margin-left: 40px;
      cursor: pointer;
    }

    input + label {
      position: relative;
      cursor: pointer;
      user-select: none;
    }

    input + label::before {
      content: "";
      position: absolute;
      top: 2px;
      left: -32px;
      width: 18px;
      height: 18px;
      background-image: url("../img/icon-check-off.svg");
      background-repeat: no-repeat;
      background-position: 0 0;
      background-size: 18px 18px;
    }

    input:checked + label::before {
      display: none;
    }

    input:checked + label::after {
      content: "";
      position: absolute;
      top: 2px;
      left: -32px;
      width: 18px;
      height: 18px;
      background-image: url("../img/icon-check-on.svg");
      background-repeat: no-repeat;
      background-size: 18px 18px;
    }
  }
}

.popup-filter__wrapper-filters-range-controls {
  position: relative;
  display: flex;
  flex-direction: column;
}

.popup-filter__wrapper-filters-range-controls-scale {
  height: 1px;
  background-color: $blog-bckg-login;
  width: 304px;
  display: flex;
  flex-direction: column;
  align-self: center;
}

.popup-filter__wrapper-filters-range-controls-scale-bar {
  width: 159px;
  height: 1px;
  background-color: $arrows-lines;
  margin-left: 61px;
}

.popup-filter__wrapper-filters-range-controls-scale-bar-toggle {
  position: absolute;
  top: -8px;
  left: 0;
  width: 18px;
  height: 18px;
  padding: 0;
  background-color: $arrows-lines;
  border-radius: 50%;
  cursor: pointer;

  &--min {
    left: 43px;

    &::before {
      content: "$ 55";
      position: absolute;
      top: 23px;
      left: -9px;
      width: 35px;
      height: 15px;
      color: $main-black;
      font-weight: 500;
      font-size: 16px;
      line-height: 20.8px;
      font-family: $font-main;
    }
  }

  &--max {
    left: 220px;

    &::before {
      content: "$ 155";
      position: absolute;
      top: 23px;
      left: -12px;
      width: 45px;
      height: 15px;
      color: $main-black;
      font-weight: 500;
      font-size: 16px;
      line-height: 20.8px;
      font-family: $font-main;
    }
  }
}

.popup-filter__wrapper-filters-button {
  width: 362px;
  height: 68px;
  box-sizing: border-box;
  border: 1px solid $arrows-lines;
  padding-top: 24px;
  padding-bottom: 28px;
  padding-left: 145px;
  padding-right: 145px;
  background-color: transparent;
  color: $main-black;
  font-family: $font-main;
  font-weight: 500;
  font-size: 16px;
  line-height: 20.8px;
  text-align: center;
  cursor: pointer;
  align-self: center;
  margin-right: 50px;

  &--submit {
    background-color: $arrows-lines;
    color: $header-footer;
    margin-top: 40px;
    margin-bottom: 25px;
  }
}

.popup-filter__wrapper-filters-list {
  display: none;
  margin: 0;
}

.popup-filter__wrapper-filters-list--opened {
  display: block;
  margin-top: 20px;
}

@media (max-width: $mobile-width) {
  .popup-filter__wrapper-filters {
    width: 280px;
  }

  .popup-filter__wrapper {
    width: 280px;

    form {
      width: 280px;

      fieldset {
        border: none;
        margin: 0;
        position: relative;
        min-height: 72px;
        padding-right: 10px;
        padding-left: 25px;
        padding-bottom: 28px;
        padding-top: 98px;
        width: 280px;

        legend {
          top: 50px;
        }

        &:nth-of-type(n+2) {
          padding-bottom: 21px;
          padding-top: 21px;

          legend {
            top: 23px;
          }

          .popup-filter__wrapper-filters-menu {
            top: 28px;
          }
        }

        &:last-of-type {
          padding-bottom: 60px;
          box-sizing: border-box;
          padding-top: 80px;
          padding-left: 15px;
          padding-right: 15px;
          width: 280px;

          legend {
            left: 25px;
          }

          .popup-filter__wrapper-filters-menu {
            right: 23px;
          }
        }

        .popup-filter__wrapper-filters-menu {
          top: 57px;
          right: 23px;
        }
      }

      input + label::before {
        left: -30px;
      }

      input:checked + label::after {
        left: -30px;
      }
    }
  }

  .popup-filter__wrapper-filters-range-controls-scale {
    width: 250px;
  }

  .popup-filter__wrapper-filters-range-controls-scale-bar {
    width: 120px;
    margin-left: 58px;
  }

  .popup-filter__wrapper-filters-range-controls-scale-bar-toggle {
    top: -8px;
    left: 0;

    &--min {
      left: 40px;
    }

    &--max {
      left: 178px;
    }
  }

  .popup-filter__wrapper-filters-button {
    width: 230px;
    padding-left: 80px;
    padding-right: 80px;
    margin-right: 0;

    &--submit {
      margin-top: 30px;
      margin-bottom: 20px;
    }
  }

  .popup-filter__wrapper-filters-closebutton {
    width: 32px;
    height: 32px;
    background-size: 32px 32px;
    right: -5px;
    top: 11px;
  }
}
