.page-main {
  min-height: calc(100vh - 252px);
  display: flex;
  flex-direction: column;
  align-items: center;
  align-self: center;
  width: 100%;
  margin-left: 15px;

  h1 {
    margin-top: 200px;
    margin-left: 0;
    margin-bottom: 0;
    align-self: center;
    font-weight: 700;
    font-size: 45px;
    line-height: 58.5px;
    font-family: $font-header;
    letter-spacing: 0.03em;
    color: $header-footer;
  }
}

.page-main__wrapper {
  display: flex;
  flex-direction: column;
  align-self: center;

  p {
    font-weight: 400;
    font-size: 38px;
    line-height: 49.4px;
    font-style: italic;
    font-family: $font-header;
    color: $header-footer;
    align-self: center;
    margin-top: -7px;
    margin-left: 0;
    margin-bottom: 0;
  }
}

.page-main-catalog {
  h1 {
    margin-top: 111px;
    margin-left: 10px;
    font-weight: 700;
    font-size: 34px;
    line-height: 44.2px;
    align-self: center;
  }
}

.page-main-catalog__wrapper {
  display: flex;
  flex-direction: column;

  p {
    font-weight: 500;
    font-size: 22px;
    line-height: 28.6px;
    font-style: italic;
    font-family: $font-header;
    color: $header-footer;
    align-self: center;
    margin-top: 0;
    margin-bottom: 0;
    text-align: center;
    margin-left: 0;
  }
}

.page-main__nav {
  align-self: center;
  display: flex;
  margin-top: 18px;
  width: 100%;
  max-width: 100%;
  flex-direction: column;
  margin-left: 23px;
  position: relative;

  &::before {
    background-color: $arrows-lines;
    content: "";
    position: absolute;
    z-index: -2;
    width: 100vw;
    bottom: -480px;
    left: -23px;
    height: 463px;
  }

  &::after {
    content: "";
    position: absolute;
    z-index: -1;
    width: 100vw;
    bottom: -480px;
    left: -23px;
    background-position: center;
    height: 463px;
    background-image: url("../img/bckg-main-dkt.jpg");
    background-repeat: no-repeat;
    background-size: cover;

    @media (min-resolution: 2dppx) {
      background-image: url("../img/bckg-main-dkt@2x.jpg");
      background-size: 100vw 463px;
    }
  }

  ul {
    display: flex;
    list-style: none;
    padding: 0;
    margin: 0;
    align-self: center;
    flex-wrap: wrap;
    margin-right: 15px;
    max-width: 495px;

    a {
      text-decoration: none;
      font-weight: 500;
      font-size: 16px;
      line-height: 24px;
      color: $main-black;
      font-family: $font-main;
    }

    li {
      &:nth-of-type(n+1) {
        margin-right: 35px;
      }

      &:nth-of-type(5n) {
        margin-right: 0;
      }

      &:nth-of-type(n+6) {
        margin-top: 35px;
      }
    }
  }
}

.page-main-catalog {
  .page-main__nav {
    &::before {
      background-color: $arrows-lines;
      content: "";
      position: absolute;
      z-index: -2;
      width: 100vw;
      bottom: -280px;
      left: -23px;
      height: 261px;
    }

    &::after {
      content: "";
      position: absolute;
      z-index: -1;
      width: 100vw;
      bottom: -280px;
      left: -23px;
      background-position: center;
      height: 261px;
      background-image: url("../img/bckg-catalog-dkt.jpg");
      background-repeat: no-repeat;
      background-size: cover;

      @media (min-resolution: 2dppx) {
        background-image: url("../img/bckg-catalog-dkt@2x.jpg");
        background-size: 100vw 261px;
      }
    }
  }
}

.page-main-card {
  .page-main__nav {
    &::before {
      display: none;
    }

    &::after {
      display: none;
    }
  }

  .page-main__new-button-prev {
    top: 45px;
    left: 7px;
    background-image: url("../img/icon-vector-brown.svg");
    background-size: 29px 9px;
    width: 29px;
    height: 9px;
  }

  .page-main__new-button-next {
    top: 45px;
    right: 7px;
    background-image: url("../img/icon-vector-brown.svg");
    transform: rotate(180deg);
    background-size: 29px 9px;
    width: 29px;
    height: 9px;
  }
}


