      padding-left: 23px;
      padding-bottom: 12px;
      padding-top: 43px;
      border-right: 1px solid $arrows-lines;
      border-left: 1px solid $arrows-lines;
      width: 250px;
      box-sizing: border-box;

      &:nth-of-type(n+1) {
        border-top: 1px solid $arrows-lines;
      }

      &:nth-of-type(n+2) {
        padding-bottom: 26px;
        padding-top: 28px;
        min-height: 66px;
      }

      &:nth-of-type(n+3) {
        padding-bottom: 26px;
        padding-top: 26px;
        min-height: 66px;
      }

      &:last-of-type {
        border-bottom: 1px solid $arrows-lines;
        padding-right: 25px;
        padding-left: 23px;
        padding-bottom: 50px;
        padding-top: 46px;
        width: 250px;
      }
    }

    legend {
      font-weight: 500;
      font-size: 16px;
      line-height: 20.8px;
      color: $main-black;
      font-family: $font-main;
      padding: 0;
      position: absolute;
      top: 26px;
    }

    ul {
      list-style: none;
      padding: 0;

      li {
        font-weight: 500;
        font-size: 14px;
        line-height: 18.2px;
        color: $main-black;
        font-family: $font-main;

        &:nth-of-type(n+2) {
          margin-top: 12px;
        }
      }
    }

    label {
      margin-left: 35px;
      cursor: pointer;
    }

    input + label {
      position: relative;
      cursor: pointer;
      user-select: none;
    }

    input + label::before {
      content: "";
      position: absolute;
      top: 3px;
      left: -25px;
      width: 15px;
      height: 15px;
      background-image: url("../img/icon-check-off.svg");
      background-repeat: no-repeat;
      background-position: 0 0;
      background-size: 15px 15px;
    }

    input:checked + label::before {
      display: none;
    }

    input:checked + label::after {
      content: "";
      position: absolute;
      top: 3px;
      left: -25px;
      width: 15px;
      height: 15px;
      background-image: url("../img/icon-check-on.svg");
      background-repeat: no-repeat;
      background-size: 15px 15px;
    }
  }
}

