.popup-card {
  position: fixed;
  overflow: auto;
  left: 8px;
  top: 90px;
  width: 100%;
  height: 100%;
  display: none;
  z-index: 50;
}

.popup-card--opened {
  display: flex;
  justify-content: center;
  background-color: $shadow-popup;
  left: 0;
  top: 0;
}

.popup-card__wrapper {
  align-self: center;
  padding-top: 20px;
  padding-left: 35px;
  padding-right: 35px;
  padding-bottom: 35px;
  box-sizing: border-box;
  width: 502px;
  min-height: 439px;
  display: flex;
  z-index: 500;
  position: relative;
  background-color: $header-footer;
  flex-direction: column;
  filter: drop-shadow(0 4px 4px $shadow-card);

  &::before {
    background-color: $header-line;
    width: 100%;
    height: 1px;
    position: absolute;
    left: 0;
    top: 246px;
    content: "";
  }

  h6 {
    font-weight: 700;
    font-size: 26px;
    line-height: 33.8px;
    color: $main-black;
    font-family: $font-header;
    margin-top: 28px;
    margin-bottom: 45px;
    width: 100%;
    text-align: center;
  }

  form {
    width: 20px;
    height: 68px;
    position: relative;
    margin-right: -10px;

    input {
      width: 25px;
      height: 70px;
      position: relative;
      border: none;
      padding: 0;
      font-weight: 400;
      font-size: 18px;
      line-height: 23.4px;
      color: $main-black;
      font-family: $font-main;
      margin-right: -10px;
    }

    input[type="number"]::-webkit-inner-spin-button,
    input[type="number"]::-webkit-outer-spin-button {
      -webkit-appearance: none;
      -moz-appearance: none;
      appearance: none;
      margin: 0;
    }

    input[type="number"] {
      -moz-appearance: textfield;
    }

    .popup-card__wrapper-number {
      cursor: pointer;
      background-color: transparent;

      &--minus {
        position: absolute;
        bottom: 4px;
        right: 8px;
        width: 14px;
        height: 2px;
        padding: 0;
        display: block;
        border: none;
        background-image: url("../img/icon-minus.svg");
        background-size: 14px 2px;
        background-repeat: no-repeat;
        z-index: 10;
      }

      &--plus {
        position: absolute;
        top: -4px;
        right: 8px;
        width: 14px;
        height: 14px;
        padding: 0;
        display: block;
        border: none;
        background-image: url("../img/icon-plus.svg");
        background-size: 14px 14px;
        background-repeat: no-repeat;
        z-index: 10;
      }
    }
  }

  div {
    &:first-of-type:not(.popup-card__wrapper-goods) {
      display: flex;
      height: 79px;
      margin-bottom: 57px;
    }
  }
}

.popup-card__wrapper-close {
  position: absolute;
  top: 20px;
  right: 21px;
  height: 14px;
  width: 14px;
  border: none;
  padding: 0;
  background-color: transparent;
  background-image: url("../img/icon-cross.svg");
  background-repeat: no-repeat;
  background-size: 14px 14px;
  cursor: pointer;

  &:hover {
    -webkit-transform: scale(1.2);
    -ms-transform: scale(1.2);
    transform: scale(1.2);
  }

  &:focus {
    -webkit-transform: rotateZ(20deg);
    -ms-transform: rotateZ(20deg);
    transform: rotateZ(20deg);
  }
}

.popup-card__wrapper-total {
  display: flex;
  justify-content: space-between;
  margin-left: 0;

  p {
    margin: 0;

    &:first-of-type {
      font-weight: 400;
      font-size: 14px;
      line-height: 18.2px;
      color: $main-black;
      font-family: $font-main;
      margin-right: 100px;
    }

    &:last-of-type {
      font-weight: 500;
      font-size: 14px;
      line-height: 18.2px;
      color: $main-black;
      font-family: $font-main;
    }
  }
}

.popup-card__wrapper-buttons {
  display: flex;
  justify-content: center;
  margin-top: 35px;

  a {
    text-decoration: none;
    width: 200px;
    height: 68px;
    padding-top: 25px;
    padding-bottom: 28px;
    font-weight: 500;
    font-size: 16px;
    line-height: 20.8px;
    font-family: $font-main;
    box-sizing: border-box;

    &:first-of-type {
      color: $main-black;
      background-color: $header-footer;
      border: 1px solid $arrows-lines;
      padding-left: 20px;
      padding-right: 20px;
      margin-right: 30px;
    }

    &:last-of-type {
      color: $header-footer;
      background-color: $arrows-lines;
      border: 1px solid $arrows-lines;
      padding-left: 60px;
      padding-right: 67px;
    }

    &:hover {
      background-color: $header-footer;
      color: $arrows-lines;
    }

    &:active {
      background-color: $blog-date-numbers;
      color: $arrows-lines;
    }
  }
}

.popup-card__wrapper-img {
  margin-top: 0;
}

.popup-card__wrapper-goods {
  display: flex;
  flex-direction: column;
  margin-left: 18px;
  margin-right: 215px;
  margin-top: -5px;

  h6 {
    font-weight: 700;
    font-size: 18px;
    line-height: 23.4px;
    color: $main-black;
    font-family: $font-header;
    margin-top: 0;
    margin-bottom: 10px;
  }

  span {
    font-weight: 400;
    font-size: 18px;
    line-height: 23.4px;
    color: $main-black;
    font-family: $font-main;
  }
}

@media (max-width: $tablet-width) {
  .popup-card {
    left: 0;
    top: 55px;
  }

  .popup-card__wrapper {
    padding-left: 30px;
    padding-right: 30px;

    form {
      .popup-card__wrapper-number {
        &--minus {
          bottom: 0;
          right: 8px;
        }

        &--plus {
          top: 0;
          right: 8px;
        }
      }
    }
  }

  .popup-card__wrapper-goods {
    margin-left: 25px;
    margin-right: 216px;
  }

  .popup-card__wrapper-buttons {
    a {
      width: 206px;

      &:first-of-type {
        padding-left: 25px;
        padding-right: 10px;
      }
    }
  }
}

@media (max-width: $mobile-width) {
  .popup-card {
    top: -47px;
  }

  .popup-card__wrapper {
    padding-top: 40px;
    padding-left: 15px;
    padding-right: 15px;
    padding-bottom: 45px;
    width: 290px;
    min-height: auto;
    height: 519px;

    div {
      &:first-of-type:not(.popup-card__wrapper-goods) {
        margin-bottom: 20px;
      }
    }

    h6 {
      font-size: 24px;
      line-height: 30px;
      margin-top: 0;
      margin-bottom: 30px;
      width: 187px;
      align-self: center;
    }

    form {
      height: 62px;

      input {
        height: 62px;
        font-size: 16px;
        line-height: 20.8px;
      }
    }
  }

  .popup-card__wrapper-close {
    position: absolute;
    top: 15px;
    right: 15px;
  }

  .popup-card__wrapper-total {
    flex-direction: column;
    margin-top: 25px;
    text-align: left;
    align-self: flex-start;
    width: 100%;

    p {
      &:first-of-type {
        font-weight: 400;
        font-size: 12px;
        line-height: 15.6px;
        margin-right: 0;
        margin-bottom: 12px;
      }

      &:last-of-type {
        font-size: 12px;
        line-height: 15.6px;
      }
    }
  }

  .popup-card__wrapper-buttons {
    flex-direction: column;
    margin-top: 30px;
    width: 260px;

    a {
      text-decoration: none;
      width: 260px;

      &:first-of-type {
        padding-left: 50px;
        padding-right: 50px;
        margin-right: 0;
        margin-bottom: 20px;
      }

      &:last-of-type {
        padding-left: 91px;
        padding-right: 91px;
      }
    }
  }

  .popup-card__wrapper-goods {
    display: flex;
    flex-direction: column;
    margin-left: 15px;
    margin-right: 70px;

    h6 {
      font-size: 16px;
      line-height: 20.8px;
    }

    span {
      font-size: 16px;
      line-height: 20.8px;
    }
  }

  .popup-card__wrapper-img {
    width: 70px;
    height: 63px;
    margin-left: 0;
    margin-top: 0;
  }
}
